<template>
    <div class="container m-0 p-0">
        <div class="root-container">
            <textarea placeholder="Note" 
                id="editing"
                spellcheck="false"
                v-model="localText"
                class="form-control p-3 m-0"
                @input="onInput"
                @keydown.enter="handleEnter"
                @keydown.tab="checkTab($event);">
            </textarea>
            <pre class="form-control p-3 m-0" id="highlighting"><code id="highlighting-content"></code><div id="content-bottom"></div></pre>
        </div>
    </div>
</template>
<script setup>
import { ref, defineModel, defineEmits, defineProps, watch, computed, onUpdated} from 'vue'
import Prism from 'prismjs';
import 'prismjs/components/prism-markdown.min.js';
let marked = require('marked')
marked.use({
    "breaks":true,"silent":true
})
const model = defineModel();
const localText = ref(model.value);
const emit = defineEmits(['input','keydown.enter']);

const props = defineProps({
    editorVisible:{type:Boolean}
})

const inputElement = computed(()=>{
    return document.getElementById("editing");
})

const onInput = () => {
    updateHighlights();
    autoGrow();
    model.value = inputElement.value.value;
    emit('input');
}

function updateHighlights() {
    let result_element = document.getElementById("highlighting-content");
    let text = inputElement.value.value;
    // Handle final newlines (see article)
    if(text[text.length-1] == "\n") {
        text += " ";
    }
    result_element.innerHTML = Prism.highlight(text, Prism.languages.markdown, "md");
}

function autoGrow() {
    inputElement.value.style.height = "5px";
    inputElement.value.style.height = (inputElement.value.scrollHeight) + "px";
}

const checkTab = (event) => {
    let code = inputElement.value.value;
    /* Tab key pressed */
    event.preventDefault(); // stop normal
    let before_tab = code.slice(0, inputElement.value.selectionStart); // text before tab
    let after_tab = code.slice(inputElement.value.selectionEnd, inputElement.value.value.length); // text after tab
    let cursor_pos = inputElement.value.selectionStart + 1; // where cursor moves after tab - moving forward by 1 char to after tab
    inputElement.value.value = before_tab + "\t" + after_tab; // add tab char
    // move cursor
    inputElement.value.selectionStart = cursor_pos;
    inputElement.value.selectionEnd = cursor_pos;
    updateHighlights(); // Update text to include indent
}

const getPrevLine = () => {
    const cursorPosition = inputElement.value.selectionStart;
    const text = inputElement.value.value;
    const lastNewLineIndex = text.lastIndexOf("\n", cursorPosition-1);
    const prevLine = text.substring(lastNewLineIndex+1, cursorPosition);
    return prevLine;
}

const handleEnter = (event) => {
    event.preventDefault();
    const nlPattern = /^\t*(\d+)\..+$/;
    const ulPattern = /^\t*- .+$/;
    const prevLine = getPrevLine();
    document.execCommand("insertText",false,"\n");

    if (prevLine.length>0){
        const nlMatch = nlPattern.exec(prevLine);
        const ulMatch = ulPattern.exec(prevLine);
        const tabMatch = /(\t+).+/.exec(prevLine);
        
        if (tabMatch) {
            const numTabs = tabMatch.at(0).split("\t").length;
            document.execCommand("insertText",false,"\t".repeat(numTabs-1));
        }
        
        if (nlMatch) {
            let nextN = parseInt(nlMatch.at(0))+1;
            document.execCommand("insertText",false,`${nextN}. `);
        } else if (ulMatch) {
            document.execCommand("insertText",false,"- ");
        }
    }
}

// Focus the div when the editor becomes visible
watch(
    () => props.editorVisible,
    (newValue) => {
        if(newValue && inputElement.value){
            setTimeout(() => {
                autoGrow();
                updateHighlights();
                inputElement.value.focus();
            }, 0);
        }
    }
)

onUpdated(autoGrow);

</script>

<style scoped>
  #editing, #highlighting, #highlighting * {
    font-family: monospace;
    font-size: 1em;
    line-height: 1.5;
    tab-size: 2;
    text-shadow: none !important;
  }

  #content-bottom {
    height: calc(70px + 2em); /* fixed bottom + a little extra */
  }

  .root-container {
    position: relative;
  }
  
  #editing, #highlighting {
    /* In the same place */
    position: absolute;
    top: 0;
    left: 0;
    overflow-y: hidden;
    overflow-x: visible;
    overflow-wrap: break-word;
    min-height: 2em;
    white-space: pre-wrap;
  }

  #highlighting {
    z-index: 0;
    background-color: transparent !important;
    border: 0;
  }
  
  #editing {
    z-index: 1;
    resize: none;
    color: transparent;
    background: transparent;
    caret-color: var(--bs-info);
  }

</style>
<style>
    .token.title.important {
        color: var(--bs-info);
    }
    .list.punctuation, 
    .token.blockquote {
        color: var(--bs-secondary);
    }
    .token.bold {
        font-weight: 600;
    }
    .token.italic {
        font-style: italic;
    }
    .token.operator {
        color: var(--bs-warning);
    }
    .token.url > .token.url {
        color: var(--bs-warning);
        text-decoration: underline;
    }
</style>